'use client'

import { useState, useEffect } from 'react'
import { useRouter } from 'next/navigation'
import { createClient } from '@/lib/supabase/client'
import Link from 'next/link'
import { use } from 'react'
import { ImageUpload } from '@/components/ImageUpload'

interface Category {
  id: string
  name: string
  icon: string | null
}

interface AttributeDefinition {
  id: string
  name: string
  display_name: string
  type: string
  options: any
  required: boolean
}

export default function NewItemPage({ params }: { params: Promise<{ id: string }> }) {
  const { id: collectionId } = use(params)
  const router = useRouter()
  const supabase = createClient()
  
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState<string | null>(null)
  const [categories, setCategories] = useState<Category[]>([])
  const [selectedCategory, setSelectedCategory] = useState<string>('')
  const [attributes, setAttributes] = useState<AttributeDefinition[]>([])
  const [attributeValues, setAttributeValues] = useState<Record<string, any>>({})
  const [pendingImages, setPendingImages] = useState<{ url: string; file?: File }[]>([])

  // Kategorien laden
  useEffect(() => {
    async function loadCategories() {
      const { data } = await supabase
        .from('categories')
        .select('*')
        .eq('collection_id', collectionId)
        .order('sort_order')
      
      if (data) setCategories(data)
    }
    loadCategories()
  }, [collectionId])

  // Attribute laden wenn Kategorie gew√§hlt
  useEffect(() => {
    async function loadAttributes() {
      if (!selectedCategory) {
        setAttributes([])
        return
      }
      
      const { data } = await supabase
        .from('attribute_definitions')
        .select('*')
        .eq('category_id', selectedCategory)
        .order('sort_order')
      
      if (data) setAttributes(data)
    }
    loadAttributes()
  }, [selectedCategory])

  async function handleSubmit(e: React.FormEvent<HTMLFormElement>) {
    e.preventDefault()
    setLoading(true)
    setError(null)

    const formData = new FormData(e.currentTarget)
    
    // Item erstellen
    const { data: item, error: insertError } = await supabase
      .from('items')
      .insert({
        collection_id: collectionId,
        category_id: selectedCategory || null,
        name: formData.get('name') as string,
        description: formData.get('description') as string || null,
        purchase_price: formData.get('purchase_price') ? parseFloat(formData.get('purchase_price') as string) : null,
        purchase_date: formData.get('purchase_date') as string || null,
        purchase_location: formData.get('purchase_location') as string || null,
        notes: formData.get('notes') as string || null,
        status: 'in_collection',
        attributes: attributeValues,
      })
      .select()
      .single()

    if (insertError) {
      setError(insertError.message)
      setLoading(false)
      return
    }

    // Bilder hochladen
    for (let i = 0; i < pendingImages.length; i++) {
      const img = pendingImages[i]
      if (img.file) {
        const fileName = `${item.id}/${Date.now()}-${img.file.name}`
        
        const { data } = await supabase.storage
          .from('item-images')
          .upload(fileName, img.file)

        if (data) {
          const { data: urlData } = supabase.storage
            .from('item-images')
            .getPublicUrl(data.path)

          await supabase.from('item_images').insert({
            item_id: item.id,
            original_url: urlData.publicUrl,
            is_primary: i === 0,
          })
        }
      }
    }

    router.push(`/collections/${collectionId}`)
  }

  function updateAttributeValue(name: string, value: any) {
    setAttributeValues(prev => ({ ...prev, [name]: value }))
  }

  return (
    <div className="p-8 max-w-3xl">
      {/* Header */}
      <div className="mb-8">
        <Link 
          href={`/collections/${collectionId}`}
          className="text-slate-500 hover:text-slate-700 text-sm flex items-center gap-1 mb-2"
        >
          ‚Üê Zur√ºck zur Sammlung
        </Link>
        <h1 className="text-3xl font-bold text-slate-900">Neues Item</h1>
        <p className="text-slate-500 mt-1">F√ºge ein neues Item zu deiner Sammlung hinzu</p>
      </div>

      <form onSubmit={handleSubmit} className="space-y-8">
        {/* Bilder */}
        <section className="bg-white rounded-xl p-6 shadow-sm border border-slate-200">
          <ImageUpload onImagesChange={setPendingImages} />
        </section>

        {/* Basis-Informationen */}
        <section className="bg-white rounded-xl p-6 shadow-sm border border-slate-200">
          <h2 className="text-lg font-semibold mb-4">Basis-Informationen</h2>
          
          <div className="space-y-4">
            <div>
              <label htmlFor="name" className="block text-sm font-medium text-slate-700 mb-2">
                Name *
              </label>
              <input
                type="text"
                id="name"
                name="name"
                required
                placeholder="z.B. '67 Camaro"
                className="w-full px-4 py-3 rounded-lg border border-slate-300 focus:ring-2 focus:ring-blue-500 focus:border-blue-500 outline-none"
              />
            </div>

            <div>
              <label htmlFor="description" className="block text-sm font-medium text-slate-700 mb-2">
                Beschreibung
              </label>
              <textarea
                id="description"
                name="description"
                rows={2}
                placeholder="Optional: Beschreibung..."
                className="w-full px-4 py-3 rounded-lg border border-slate-300 focus:ring-2 focus:ring-blue-500 focus:border-blue-500 outline-none resize-none"
              />
            </div>

            {categories.length > 0 && (
              <div>
                <label htmlFor="category" className="block text-sm font-medium text-slate-700 mb-2">
                  Kategorie
                </label>
                <select
                  id="category"
                  value={selectedCategory}
                  onChange={(e) => setSelectedCategory(e.target.value)}
                  className="w-full px-4 py-3 rounded-lg border border-slate-300 focus:ring-2 focus:ring-blue-500 focus:border-blue-500 outline-none"
                >
                  <option value="">Keine Kategorie</option>
                  {categories.map((cat) => (
                    <option key={cat.id} value={cat.id}>
                      {cat.icon} {cat.name}
                    </option>
                  ))}
                </select>
              </div>
            )}
          </div>
        </section>

        {/* Kauf-Informationen */}
        <section className="bg-white rounded-xl p-6 shadow-sm border border-slate-200">
          <h2 className="text-lg font-semibold mb-4">Kauf-Informationen</h2>
          
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <label htmlFor="purchase_price" className="block text-sm font-medium text-slate-700 mb-2">
                Kaufpreis (‚Ç¨)
              </label>
              <input
                type="number"
                id="purchase_price"
                name="purchase_price"
                step="0.01"
                min="0"
                placeholder="0.00"
                className="w-full px-4 py-3 rounded-lg border border-slate-300 focus:ring-2 focus:ring-blue-500 focus:border-blue-500 outline-none"
              />
            </div>

            <div>
              <label htmlFor="purchase_date" className="block text-sm font-medium text-slate-700 mb-2">
                Kaufdatum
              </label>
              <input
                type="date"
                id="purchase_date"
                name="purchase_date"
                className="w-full px-4 py-3 rounded-lg border border-slate-300 focus:ring-2 focus:ring-blue-500 focus:border-blue-500 outline-none"
              />
            </div>

            <div className="md:col-span-2">
              <label htmlFor="purchase_location" className="block text-sm font-medium text-slate-700 mb-2">
                Gekauft bei
              </label>
              <input
                type="text"
                id="purchase_location"
                name="purchase_location"
                placeholder="z.B. eBay, Flohmarkt, H√§ndler..."
                className="w-full px-4 py-3 rounded-lg border border-slate-300 focus:ring-2 focus:ring-blue-500 focus:border-blue-500 outline-none"
              />
            </div>
          </div>
        </section>

        {/* Dynamische Attribute */}
        {attributes.length > 0 && (
          <section className="bg-white rounded-xl p-6 shadow-sm border border-slate-200">
            <h2 className="text-lg font-semibold mb-4">Kategorie-Attribute</h2>
            
            <div className="space-y-4">
              {attributes.map((attr) => (
                <DynamicField 
                  key={attr.id} 
                  attribute={attr} 
                  value={attributeValues[attr.name]}
                  onChange={(value) => updateAttributeValue(attr.name, value)}
                />
              ))}
            </div>
          </section>
        )}

        {/* Notizen */}
        <section className="bg-white rounded-xl p-6 shadow-sm border border-slate-200">
          <h2 className="text-lg font-semibold mb-4">Notizen</h2>
          <textarea
            id="notes"
            name="notes"
            rows={3}
            placeholder="Pers√∂nliche Notizen..."
            className="w-full px-4 py-3 rounded-lg border border-slate-300 focus:ring-2 focus:ring-blue-500 focus:border-blue-500 outline-none resize-none"
          />
        </section>

        {error && (
          <div className="bg-red-50 text-red-600 p-4 rounded-lg">
            {error}
          </div>
        )}

        <div className="flex gap-4">
          <button
            type="submit"
            disabled={loading}
            className="flex-1 bg-blue-600 text-white px-6 py-3 rounded-lg hover:bg-blue-700 transition-colors disabled:opacity-50"
          >
            {loading ? 'Speichere...' : 'Item speichern'}
          </button>
          <Link
            href={`/collections/${collectionId}`}
            className="px-6 py-3 rounded-lg border border-slate-300 hover:bg-slate-50 transition-colors"
          >
            Abbrechen
          </Link>
        </div>
      </form>
    </div>
  )
}

function DynamicField({ 
  attribute, 
  value, 
  onChange 
}: { 
  attribute: AttributeDefinition
  value: any
  onChange: (value: any) => void
}) {
  const { name, display_name, type, options, required } = attribute

  switch (type) {
    case 'text':
      return (
        <div>
          <label className="block text-sm font-medium text-slate-700 mb-2">
            {display_name} {required && '*'}
          </label>
          {options?.multiline ? (
            <textarea
              value={value || ''}
              onChange={(e) => onChange(e.target.value)}
              rows={2}
              className="w-full px-4 py-3 rounded-lg border border-slate-300 focus:ring-2 focus:ring-blue-500 outline-none resize-none"
              required={required}
            />
          ) : (
            <input
              type="text"
              value={value || ''}
              onChange={(e) => onChange(e.target.value)}
              className="w-full px-4 py-3 rounded-lg border border-slate-300 focus:ring-2 focus:ring-blue-500 outline-none"
              required={required}
            />
          )}
        </div>
      )

    case 'number':
      return (
        <div>
          <label className="block text-sm font-medium text-slate-700 mb-2">
            {display_name} {required && '*'}
          </label>
          <input
            type="number"
            value={value || ''}
            onChange={(e) => onChange(e.target.value ? parseInt(e.target.value) : null)}
            min={options?.min}
            max={options?.max}
            step={options?.step || 1}
            className="w-full px-4 py-3 rounded-lg border border-slate-300 focus:ring-2 focus:ring-blue-500 outline-none"
            required={required}
          />
        </div>
      )

    case 'select':
      return (
        <div>
          <label className="block text-sm font-medium text-slate-700 mb-2">
            {display_name} {required && '*'}
          </label>
          <select
            value={value || ''}
            onChange={(e) => onChange(e.target.value)}
            className="w-full px-4 py-3 rounded-lg border border-slate-300 focus:ring-2 focus:ring-blue-500 outline-none"
            required={required}
          >
            <option value="">Bitte w√§hlen...</option>
            {options?.choices?.map((choice: string) => (
              <option key={choice} value={choice}>{choice}</option>
            ))}
          </select>
        </div>
      )

    case 'checkbox':
      return (
        <div className="flex items-center gap-3">
          <input
            type="checkbox"
            id={name}
            checked={value || false}
            onChange={(e) => onChange(e.target.checked)}
            className="w-5 h-5 rounded border-slate-300 text-blue-600 focus:ring-blue-500"
          />
          <label htmlFor={name} className="text-sm font-medium text-slate-700">
            {display_name}
          </label>
        </div>
      )

    case 'date':
      return (
        <div>
          <label className="block text-sm font-medium text-slate-700 mb-2">
            {display_name} {required && '*'}
          </label>
          <input
            type="date"
            value={value || ''}
            onChange={(e) => onChange(e.target.value)}
            className="w-full px-4 py-3 rounded-lg border border-slate-300 focus:ring-2 focus:ring-blue-500 outline-none"
            required={required}
          />
        </div>
      )

    case 'tags':
      return (
        <div>
          <label className="block text-sm font-medium text-slate-700 mb-2">
            {display_name} {required && '*'}
          </label>
          <input
            type="text"
            value={Array.isArray(value) ? value.join(', ') : value || ''}
            onChange={(e) => onChange(e.target.value.split(',').map(s => s.trim()).filter(Boolean))}
            placeholder="Komma-getrennt eingeben..."
            className="w-full px-4 py-3 rounded-lg border border-slate-300 focus:ring-2 focus:ring-blue-500 outline-none"
          />
        </div>
      )

    default:
      return (
        <div>
          <label className="block text-sm font-medium text-slate-700 mb-2">
            {display_name}
          </label>
          <input
            type="text"
            value={value || ''}
            onChange={(e) => onChange(e.target.value)}
            className="w-full px-4 py-3 rounded-lg border border-slate-300 focus:ring-2 focus:ring-blue-500 outline-none"
          />
        </div>
      )
  }
}
